import { createSlice } from "redux-starter-kit";

const slice = createSlice({
  slice: "title",
  initialState: {
    title: ""
  },
  reducers: {
    setTitle: (state, action) => ({
      title: action.payload
    })
  }
});
// cao state cua ham day dau
export const toDashbroad = () => dispatch => {
  dispatch(slice.actions.setTitle("Thng k"));
};

export const toStudent = () => dispatch => {
  dispatch(slice.actions.setTitle("Qun l sinh vin"));
};

export const toClasses = () => dispatch => {
  dispatch(slice.actions.setTitle("Qun l lp hc"));
};

export const toSubject = () => dispatch => {
  dispatch(slice.actions.setTitle("Qun l mn hc"));
};

export const toProgramme = () => dispatch => {
  dispatch(slice.actions.setTitle("Qun l chng trnh hc"));
};

export const toSetting = () => dispatch => {
  dispatch(slice.actions.setTitle("Ci t"));
};

export const toHelp = () => dispatch => {
  dispatch(slice.actions.setTitle("Tr gip"));
};

export const toContact = () => dispatch => {
  dispatch(slice.actions.setTitle("Lin h vi VTC Academy"));
};

export default slice;
