import React from "react";
import moment from "moment";

import Label from "../../../../theme/FormElement/Label";
import Textarea from "../../../../theme/FormElement/Textarea";

const Content = ({ data }) => {
  let birthday = data.birthday.toString();
  let admissionDate = data.admissionDate.toString();
  return (
    <React.Fragment>
      <div className="form detailForm">
        <Label
          classes="formItem"
          labelName="H v tn:"
          type="text"
          value={data.fullName}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Chuyn nghnh:"
          type="text"
          value={data._Programme.programmeName}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Trng thi:"
          type="text"
          value={data._StudentStatus.ssName.replace("", "")}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Ngy nhp hc:"
          type="text"
          value={moment(admissionDate).format("DD/MM/YYYY")}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Gii tnh:"
          type="text"
          value={data.gender === "M" ? "Nam" : "N"}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Ngy sinh:"
          type="text"
          value={moment(birthday).format("DD/MM/YYYY")}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="S CMND:"
          type="text"
          value={data.idCardNo}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="in thoi:"
          type="text"
          value={data.phoneNo}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Email:"
          type="text"
          value={data.email}
          readOnly={true}
        />
        <Label
          classes="formItem"
          labelName="Email c nhn:"
          type="text"
          value={data.personalEmail}
          readOnly={true}
        />

        <Textarea
          classes="formItem"
          labelName="a ch:"
          rows={2}
          value={data.address}
          readOnly={true}
        />

        <Textarea
          classes="formItem"
          labelName="Ghi ch:"
          rows={2}
          value={data.note}
          readOnly={true}
        />
      </div>
    </React.Fragment>
  );
};

export default Content;
